@font-face {
    font-family: outfit_light;
    src: url('./assets/Outfit-Light.ttf');
  }

.nav{
    size:medium;
    display:flex;
    justify-content:space-between;
    align-items:center;
    background-color:rgb(64, 66, 73);
    color:black;
    height:6vh;
    position:relative;
    box-shadow: 5px 3px 10px rgb(64, 66, 73) ;
    padding-right:1em;
    
}
.nav a {
    text-decoration:none;
    color:black;
    cursor:pointer;
}
.nav a:active a:visited {
    color:rgb(33, 34, 77);
}

.nav p{
    display:flex;
    justify-content:space-between;
    gap:10px;
    align-items:center;
    width:max-content;
    cursor:pointer;
}
.logo{
    
    position:absolute;
    width:10%;
    left: 45%;
    font-family:outfit_light;
    font-size:xx-large;
    cursor:pointer;
    
}
.profile{
    display:flex;
    font-size:larger;
    width:2em;
    height:auto;
    margin:0.5em;
    color:white;
    cursor:pointer;
}
.matches{
    display:flex;
    font-size:larger;
    width:2.5em;
    height:auto;
    margin:0 0;
    color:white;
    cursor:pointer;
}
.logout{
    cursor:pointer;
    color:black;
    padding:0;
    border-style:none;
    border:none;
    background-color:transparent;
    font-size:1em;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}
.app{
    background-color:rgb(182, 182, 185);
    text-align:center;
    height:100vh;
    background-image:url('./assets/background-pic.jpg');
    background-repeat:no-repeat;
    background-size:cover;
    overflow-y:hidden;
    

}
.main{
    max-height:96vh;
    overflow-y:hidden;
    scrollbar-width:0;
    scrollbar-color:transparent;
}
.wrapper{
    max-height:90vh;
    overflow: scroll;
    scrollbar-width:0;
    scrollbar-color:transparent;
    
    
}
::-webkit-scrollbar {
    display: none;
}

.sheet{
    display:flex;
    flex-direction:column;
    box-shadow: 5px 10px 18px rgb(64, 66, 73) ;
    border-radius:30px;
    width:50%;
    background-color:rgb(64, 66, 73);
    margin: 2em 0 2em 25%;
    color:black;
    position:relative;
    padding:1em 0 0.5em 0;
    
    
    
}
.sheet h1{
    font-weight:lighter;
    margin:0.5em 0 0;
}
.sheet input{
    width:40%;
    align-self:center;
}

.sheet button{
    
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}
#button{
    width:min-content;
    align-self:center;
    background-color:white;
    border-style:solid;
    border-radius:5px;
    border-color:transparent;
    margin: 0.5em 0;
    color:black;
    cursor:pointer;
    box-shadow: 2px 2px 10px turquoise;
    margin: 0.25em 0.25em 0;
    

}


#users{
    margin-top:1em;

}
#register{
    background-color:transparent;
    border-style:none;
    color:black;
    text-decoration:underline;
    margin-top:0.7em;
    font-size:medium;
    font-weight:lighter;

}

#register:active{
    color:black;
}
.choice:disabled{
    background-color:turquoise;
    

}
.choice{
    background-color:darkgrey;
    border-style:solid;
    border-radius:5px;
    border-color:transparent;
    margin: 0 0.25em 0;
    color:black;
    cursor:pointer;
    max-width:max-content;
    
}

.false{
    background-color:darkgrey;
    border-style:solid;
    border-radius:5px;
    border-color:transparent;
    margin: 0 0.25em 0;
    color:black;
    cursor:pointer;
    max-width:max-content;
}

.true{
    background-color:turquoise;
    border-style:solid;
    border-radius:5px;
    border-color:transparent;
    margin: 0 0.25em 0;
    color:black;
    cursor:pointer;
    max-width:max-content;

}
.biglink{
    color:black;
    text-decoration:none;
}

#activating{
    position:absolute;
    right:1.5em;
    top:1.5em;
    
}
#jobDescription, #bio{
    width:40%;
    align-self:center;
    border-style:none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}
.contact{
    background-color:transparent;
    color:white;
    size:xx-large;
     display:flex;
    font-size:larger;
    width:2em;
    height:auto;
    
}
#contactbutton{

    background-color:transparent;
    color:none;
    border-style:none;
    border-color:transparent;
    width:40px;
    size:xx-large;
}
.skills{
    display:flex;
    flex-direction:column;
    align-self:center;
    justify-content:center;
    gap:0.5em;
}
/* 
// range button */

.range_container {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 35% auto;
  }
  
  .sliderscontrol {
    position: relative;
    min-height: 50px;
  }
  
  .form_control {
    position: relative;
    display: flex;
    justify-content: space-between;
    font-size: 24px;
    color: #635a5a;
  }
  
  input[type='range']::-webkit-slider-thumb {
    -webkit-appearance: none;
    pointer-events: all;
    width: 24px;
    height: 24px;
    background-color: #fff;
    border-radius: 50%;
    box-shadow: 0 0 0 1px #C6C6C6;
    cursor: pointer;
  }
  
  input[type='range']::-moz-range-thumb {
    
    -webkit-appearance: none;
    pointer-events: all;
    width: 24px;
    height: 24px;
    background-color: #fff;
    border-radius: 50%;
    box-shadow: 0 0 0 1px #C6C6C6;
    cursor: pointer;  
  }
  
  input[type='range']::-webkit-slider-thumb:hover {
    background: #802828;
  }
  
  input[type='range']::-webkit-slider-thumb:active {
    box-shadow: inset 0 0 3px #387bbe, 0 0 9px #387bbe;
    -webkit-box-shadow: inset 0 0 3px #387bbe, 0 0 9px #387bbe;
  }
  
  input[type="number"] {
    color: #8a8383;
    width: 50px;
    height: 30px;
    font-size: 20px;
    border: none;
  }
  
  input[type='number']::-webkit-inner-spin-button, 
  input[type='number']::-webkit-outer-spin-button {  
     opacity: 1;
  }
  
  input[type="range"] {
    -webkit-appearance: none; 
    appearance: none;
    height: 2px;
    width: 100%;
    position: absolute;
    background-color: #C6C6C6;
    pointer-events: none;
  }
  
  #slider-1 {
    height: 0;
    z-index: 1;
  }
